*, :before, :after {
  box-sizing: border-box;
}

:root {
  --text-color: black;
  --accent-color: blue;
}

html {
  height: 100%;
}

body {
  height: 100%;
  grid-template-rows: auto 1fr auto;
  gap: 1em;
  margin: 3em;
  font-family: IBM Plex Serif, serif;
  display: grid;
}

a {
  color: var(--accent-color);
  border-bottom: 1px solid;
  text-decoration: none;
}

button {
  cursor: pointer;
  font-family: IBM Plex Serif, serif;
  font-size: 1em;
}

kbd {
  background-color: #eee;
  border-radius: 5px;
  margin: .1em;
  padding: .2em .5em;
  display: inline-block;
  box-shadow: 1px 1px 5px #0000000d;
}

header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.title {
  margin: 0;
}

.title a {
  color: var(--text-color);
  border: none;
}

header .nav ul {
  padding: 0;
  list-style-type: none;
}

header .nav__items {
  margin: 0;
  display: flex;
}

header .nav__item {
  margin: 0;
  padding: 10px;
}

header .nav__item a {
  display: inline-block;
}

.editor-nav ul {
  padding: 0;
  list-style-type: none;
}

.editor-nav .nav__items {
  margin: 0;
  display: flex;
}

.editor-nav .nav__item button {
  background: none;
  border: none;
  padding: 10px;
}

.editor-nav .nav__item > button:hover, .editor-nav .nav__item--open > button {
  background-color: #eee;
}

.editor-nav .nav__dropdown {
  z-index: 5;
  display: none;
  position: absolute;
  box-shadow: 1px 1px 5px #0003;
}

.editor-nav .nav__item--open .nav__dropdown {
  display: block;
}

.editor-nav .nav__dropdown .nav__dropdown-item button {
  width: 100%;
  text-align: left;
  background-color: #eee;
  display: block;
}

.editor-nav .nav__dropdown .nav__dropdown-item button:hover {
  background-color: #ccc;
}

main {
  grid-template-rows: auto 1fr;
  display: grid;
}

.editorHeader {
  align-items: center;
  gap: 1em;
  display: flex;
}

.filename-ext {
  color: #aaa;
}

.code {
  grid-template-rows: 1fr 150px;
  grid-template-columns: 50% 50%;
  gap: 1em;
  display: grid;
}

.editor {
  grid-row: 1 / span 2;
}

.output {
  white-space: pre;
  padding: 1em;
  font-family: IBM Plex Mono, monospace;
  font-size: 24px;
}

.shortcuts {
  display: none;
}

.shortcuts .backdrop {
  background-color: #0000004d;
  position: fixed;
  inset: 0;
}

.shortcuts .window {
  width: 50%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  padding: 50px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 5px 5px 20px #0003;
}

.shortcuts .window dl > div {
  margin: 1em 0;
}

footer {
  margin: 20px 100px;
}

